import "./styles.css";
import Expenses from "./Expenses";
import { useCallback, useState } from "react";
import NewExpense from "./NewExpense";

export default function App() {
  const [showForm, setShowForm] = useState(false);
  const [newExpenseAddedAt, setNewExpenseAddedAt] = useState(null);

  const openForm = useCallback(() => {
    setShowForm(true);
  }, []);

  const closeForm = useCallback(() => {
    setShowForm(false);
  }, []);

  if (showForm) {
    return (
      <NewExpense
        closeForm={closeForm}
        setNewExpenseAddedAt={setNewExpenseAddedAt}
      />
    );
  }

  return <Expenses openForm={openForm} key={newExpenseAddedAt} />;
}
